.header {
  width: 100%;
  background-image: url(/src/images/header-background-primary-320.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding-top: 40px;
  padding-bottom: 44px;
  color: white;
  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url(/src/images/header-background-primary-640.png);
  }
  @media screen and (min-width: 768px) {
    background-image: url(/src/images/header-background-primary-768.png);
  }
  @media screen and (min-device-pixel-ratio: 2) and (min-width: 768px),
    screen and (min-resolution: 192dpi) and (min-width: 768px),
    screen and (min-resolution: 2dppx) and (min-width: 768px) {
    background-image: url('/src/images/header-background-primary-1536.png');
  }
  @media screen and (min-width: 1280px) {
    background-image: url('/src/images/header-background-primary-1280.png');
  }
  @media screen and (min-device-pixel-ratio: 2) and (min-width: 1280px),
    screen and (min-resolution: 192dpi) and (min-width: 1280px),
    screen and (min-resolution: 2dppx) and (min-width: 1280px) {
    background-image: url('/src/images/header-background-primary-2560.png');
  }
  &__main {
    margin-bottom: 54px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__title {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  &__logo {
    padding-top: 2px;
    display: inline-block;
    vertical-align: middle;
    transition: transform 0.5s ease;
    transform-style: preserve-3d;
  }
  &__logo--animated {
    animation: rotateCarousel 4s infinite linear;
  }

  &__name {
    display: none;
    font-size: 30px;

    @include tablet {
      display: block;
    }
  }

  &__nav {
    display: flex;
    gap: 40px;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 500;
    color: white;
  }
  &__item {
    position: relative;
    cursor: pointer;

    &::after {
      content: '';
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: red;
      transform: scaleX(0);
      transition: transform 0.3s ease;
    }
    &:hover::after,
    &:focus::after {
      transform: scaleX(1);
    }
  }
  &__active {
    position: relative;
    cursor: pointer;

    &::after {
      content: '';
      position: absolute;
      bottom: -3px;
      left: 0;
      width: 100%;
      height: 3px;
      background-color: red;
    }
  }

  &__form {
    position: relative;
    max-width: 394px;
    margin: 0 auto;
    display:flex;
    align-items: center;
  }

  &__input {
    flex:1;
    width: 100%;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid white;
    padding: 0;
    color: white;

    &::placeholder {
      color: white;
    }

    &:focus {
      outline: none;
      border-color: #ff6b08;
      box-shadow: 0 2px 4px 0 #ff6b08;
    }
  }

 // &__icon {
 //   position: absolute;
 //   right: 0;
  //  top: 0.2rem;
  //}

  &--library {
    background-image: url('/src/images/header-background-secondary-320.png');
    // background-size: cover;
    padding-bottom: 60px;
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url(/src/images/header-background-secondary-640.png);
    }
    @media screen and (min-width: 768px) {
      background-image: url(/src/images/header-background-secondary-768.png);
    }
    @media screen and (min-device-pixel-ratio: 2) and (min-width: 768px),
      screen and (min-resolution: 192dpi) and (min-width: 768px),
      screen and (min-resolution: 2dppx) and (min-width: 768px) {
      background-image: url('/src/images/header-background-secondary-1536.png');
    }
    @media screen and (min-width: 1280px) {
      background-image: url('/src/images/header-background-secondary-1280.png');
    }
    @media screen and (min-device-pixel-ratio: 2) and (min-width: 1280px),
      screen and (min-resolution: 192dpi) and (min-width: 1280px),
      screen and (min-resolution: 2dppx) and (min-width: 1280px) {
      background-image: url('/src/images/header-background-secondary-2560.png');
    }

    &__buttons {
      padding-top: 12px;
      display: flex;
      gap: 20px;
      justify-content: center;
    }
  }
}

.submit-button {
  display: inline;
  background-image: url(/src/images/search_icon.svg);
  width: 10px;
  height: 10px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  /* put the height and width of your image here */
  // height: 50px;
  // width: 200px;
  border: none;
  background-color: transparent;
  margin-left: -20px;
  cursor:pointer;
  vertical-align: middle;
}
@keyframes rotateCarousel {
  from {
    transform: rotateY(0deg);
  }

  to {
    transform: rotateY(360deg);
  }
}
